<template>
  <div>
    <div class="recharge-methods">
      <div class="recharge-pay-item" :class="{'selected': item.key === curSelect}" @click="doSelect(item.key)" v-for="item in payWay" :key="item.key">
        <div class="pay-logo" :class="'logo-'+ item.key"><i></i>{{item.name}}</div>
        <div class="pay-selected"><i></i></div>
      </div>
    </div>
    <div class="recharge-btn">
      <a href="javascript:;" :class="{'disable': paying}" @click="goPay">
        <span v-if="!paying">
          <span v-if="!repay">立即支付￥{{money|moneyFormate}}</span>
          <span v-else>重新支付￥{{money|moneyFormate}}</span>
        </span>
        <span v-else>支付中...</span>
      </a>
      <a href="javascript:;" v-if="hasCancel" class="cancel-btn" @click="deleteRecharge">取消充值订单</a>
    </div>
  </div>
</template>

<script>
import util from '@/utils/utils'
import URL from '@/http/url'
export default {
  name: 'paymethod',
  props: {
    mobile: {
      default: ''
    },
    orderSn: {
      default: ''
    },
    money: {
      default: 0
    },
    hasCancel: {
      default: false
    },
    repay: {
      default: false
    }
  },
  data() {
    return {
      curSelect: 'alipay',
      paying: false,
      payWay: [
        { 'key': 'alipay', name: '支付宝支付' },
        { 'key': 'weixin', name: '微信支付' }
      ]
    }
  },
  filters: {
    moneyFormate(money) {
      return util.formatNumber(money)
    }
  },
  methods: {
    doSelect(key) {
      this.curSelect = key
      // this.$emit('selectedFn', key)
    },
    checkOrderInfo() {
      return this.$http.post(URL.RECHARGE_PHONE_SAVEORDER, {
        'lifePayType': 'mobile',
        'payAmt': this.money, // 实缴金额
        'orderAmt': this.money, // 应缴金额
        'diamondCount': 0,
        'phoneConfigId': '',
        'rechargeValue': this.money,
        'billKey': this.mobile,
        'version': 1
      })
    },
    doPay(url, orderSn) {
      if (this.curSelect === 'alipay') {
        util.openNewWindow(`${url}?orderSn=${orderSn}`)
      } else if (this.curSelect === 'weixin') {
        this.$http.get(`${url}?orderSn=${orderSn}`).then(res => {
          let result = res.data;
          if (result.success) {
            if (result.data.returnCode === 'SUCCESS' && result.data.resultCode === 'SUCCESS') {
              util.openNewWindow(result.data.mwebUrl, true)
            } else {
              this.$toast(result.data.returnMsg)
            }
          } else {
            this.$toast(result.message)
          }
          this.paying = false;
        })
      }
    },
    goPay() {
      // if (Number(this.money) !== 50 && Number(this.money) !== 100) {
      //   this.$toast('暂不支持的金额')
      //   return
      // }
      if (this.paying) return;
      this.paying = true;
      let url = ''
      if (this.curSelect === 'alipay') {
        url = URL.PHONE_RECHARGE_ALIPAY
      } else if (this.curSelect === 'weixin') {
        url = URL.PHONE_RECHARGE_WEIXIN
      }
      if (this.hasCancel || this.repay) {
        this.doPay(url, this.orderSn)
      } else {
        this.checkOrderInfo().then(res => {
          let result = res.data
          if (result.success) {
            let orderSn = result.data.orderSn
            this.doPay(url, orderSn)
          } else {
            if (result.errorCode === '1100') {
              this.$toast('请重新登录')
              let timer = setTimeout(() => {
                clearTimeout(timer);
                this.$store.dispatch('doLogin')
              }, 2000)
              return
            }
            if (result.message === '您有未付款的充值订单，请优先处理!') {
              this.$dialog.confirm({
                title: '充值提示',
                message: '您有未付款的充值订单，请优先处理!',
                confirmButtonText: '去处理'
              }).then(() => {
                this.$router.push('/rechargelist')
              }).catch(() => {
                // on cancel
              });
              this.paying = false;
              return;
            }
            this.$toast(result.message)
            this.paying = false;
          }
        }).catch(() => {
          this.paying = false;
        })
      }
    },
    deleteRecharge() {
      this.$dialog.confirm({
        title: '订单状态变更提醒',
        message: '确定取消当前充值订单吗？',
        confirmButtonText: '确定'
      }).then(() => {
        this.$http.get(`${URL.DELETE_RECHARGE}?orderSn=${this.orderSn}`).then(res => {
          let result = res.data
          if (result.success) {
            this.$router.replace('/rechargelist')
          } else {
            this.$toast(result.message)
          }
        })
      }).catch(() => {
        // on cancel
      });
    }
  }
}
</script>

