<template>
  <div class="recharge-page">
    <Header>
      <router-link to="/rechargelist" slot="right">充值记录</router-link>
    </Header>
    <div class="recharge-info">
      <div class="recharge-phone">
        <p>充值手机号</p>
        <h2>{{mobile | mobileFormate}}</h2>
        <small v-if="areaStatus">{{area}}</small>
        <small v-else class="notice-text">未知区域，请谨慎充值</small>
      </div>
      <div class="recharge-money">
        <span>充值金额</span>
        <span v-if="money == 50 || money == 100">￥{{money | moneyFormate}}</span>
        <span v-else>暂不支持的金额</span>
      </div>
    </div>
    <payMethod :money="money" :mobile="mobile"/>
    <div class="recharge-tips">温馨提示：本业务不提供发票</div>
  </div>
</template>

<script>
import util from '@/utils/utils'
import URL from '@/http/url'
import Header from '@/components/header'
import payMethod from './components/payMethod'
export default {
  name: 'recharge',
  components: {
    Header,
    payMethod,
  },
  filters: {
    mobileFormate: function (phone) {
      if (!phone) return ''
      let arr = phone.split('')
      let str = ''
      arr.forEach(function(val, ind) {
        if (ind === 3 || ind === 7) {
          str += ' '
        }
        str += val
      })
      return str
    },
    moneyFormate(money) {
      return util.formatNumber(money)
    }
  },
  data() {
    return {
      mobile: '', // 充值手机号
      money: '', // 充值金额
      area: '默认号码', // 地区
      areaStatus: false, // 归属地状态
      paying: false, // 支付中
      payMethod: '', // 支付方式
    }
  },
  mounted () {
    let { money, mobile } = this.$route.query;
    if (!util.isPhone(mobile)) {
      this.$toast('手机号格式不正确,请重新输入');
      // let timer = setTimeout(() => {
      //   clearTimeout(timer)
      //   this.$store.dispatch('doHistoryBack')
      // }, 2000)
      // return
    }

    this.mobile = mobile;
    this.money = money;
    this.getPhoneLocation()
  },
  methods: {
    getPhoneLocation() {
      this.$http.get(`${URL.GET_PHONE_LOCATION}?mobile=${this.mobile}`).then(res => {
        let result = res.data
        if (result.success) {
          this.area = result.data
          this.areaStatus = true
        } else {
          this.areaStatus = false
        }
      })
    },

  }
}
</script>

